* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.container {
  max-width: 1100px;
  margin: 0 auto;
}
.body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #ffffff;
}
.body--scrolloff {
  overflow: hidden;
}
.body--scrolloff::before {
  content: '';
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.5);
}
.header {
  width: 100%;
  position: fixed;
  z-index: 2;
}
.header--invert {
  background-color: #ffffff;
  position: sticky;
  top: 0;
}
.header__wrap {
  display: grid;
  grid-template-columns: min-content 1fr;
  position: relative;
  align-items: center;
  padding: 20px;
}
@media (max-width: 900px) {
  .header__wrap {
    padding: 0 20px;
  }
}
.header__title {
  font-size: 20px;
  font-weight: normal;
  text-transform: uppercase;
  color: #ffffff;
  white-space: nowrap;
}
.header__title--invert {
  color: #282828;
}
.header__menu-button {
  width: min-content;
  height: min-content;
  border: none;
  background-color: transparent;
  cursor: pointer;
  display: none;
}
@media (max-width: 900px) {
  .header__menu-button {
    display: flex;
    justify-content: center;
    align-items: center;
    justify-self: end;
    z-index: 1;
  }
}
.nav {
  display: flex;
  justify-content: end;
  align-items: center;
}
@media (max-width: 900px) {
  .nav {
    position: absolute;
    width: 100%;
    top: -200%;
    transition: all 0.5s ease-in;
  }
}
.nav--open {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  transition: all 0.5s ease;
  justify-content: center;
  background-color: #ffffff;
}
.nav--open .nav__list {
  padding: 80px 20px;
  pointer-events: all;
  opacity: 1;
  transition: all 0.5s ease;
}
.nav--open .nav__link {
  color: #282828;
  font-weight: normal ;
}
.nav--open .nav__link:hover::after,
.nav--open .nav__link:focus::after {
  background-color: #282828;
}
.nav__list {
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 20px;
  list-style: none;
}
@media (max-width: 900px) {
  .nav__list {
    flex-direction: column;
    justify-content: start;
    align-items: center;
    width: 100%;
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s ease;
  }
}
@media (max-width: 900px) {
  .nav__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
}
.nav__link {
  padding: 8px 0;
  position: relative;
  color: #ffffff;
  text-decoration: none;
  font-weight: bold;
  font-size: 18px;
  opacity: 0.7;
  transition: 0.2s all ease-in;
}
.nav__link:hover,
.nav__link:focus {
  opacity: 1;
  transition: 0.2s all ease-in;
}
.nav__link:hover::after,
.nav__link:focus::after {
  position: absolute;
  content: '';
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  transition: 0.2s all ease-in;
  border-radius: 20%;
}
.nav__link--invert {
  color: #282828;
  font-weight: normal;
}
.nav__link--invert:hover::after,
.nav__link--invert:focus::after {
  background-color: #282828;
}
.nav__link--current {
  opacity: 1;
  pointer-events: none;
}
@media (max-width: 900px) {
  .nav__link {
    width: 100%;
    font-size: 22px;
    text-align: center;
  }
}
.ham {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.hamRotate.active {
  transform: rotate(45deg);
}
.line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #000;
  stroke-width: 5.5;
  stroke-linecap: round;
}
.ham7 .top {
  stroke-dasharray: 40 82;
}
.ham7 .middle {
  stroke-dasharray: 40 111;
}
.ham7 .bottom {
  stroke-dasharray: 40 161;
}
.ham7.active .top {
  stroke-dasharray: 17 82;
  stroke-dashoffset: -62px;
}
.ham7.active .middle {
  stroke-dashoffset: 23px;
}
.ham7.active .bottom {
  stroke-dashoffset: -83px;
}
.main {
  width: 100%;
  height: 100vh;
  min-height: 600px;
}
.sliderfull {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  position: relative;
}
.slederfull-controls {
  height: min-content;
  position: absolute;
  z-index: 1;
  bottom: 80px;
  left: 50%;
  transform: translate(-50%, -50%);
  width: min-content;
  display: grid;
  grid-template-columns: repeat(2, min-content);
  align-items: center;
  grid-column-gap: 20px;
}
.slederfull-controls::before {
  content: '';
  position: absolute;
  width: 4px;
  height: 100%;
  right: 50%;
  transform: translateX(100%) rotate(15deg);
  top: 0;
  background-color: #ffffff;
}
.slederfull-controls__total {
  font-weight: bold;
  font-family: 'Lato', sans-serif;
  color: #ffffff;
}
.slederfull-controls__total--current {
  font-size: 23px;
  align-self: start;
}
.slederfull-controls__total--total {
  font-size: 18px;
  opacity: 0.7;
  align-self: end;
}
.sliderfull-items__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 50px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 1;
}
.sliderfull-items__button--next {
  right: 10px;
}
.sliderfull-items__button--prev {
  left: 10px;
}
.sliderfull-items__button-img {
  width: 100%;
  height: 100%;
}
.sliderfull-items__button-img:hover {
  opacity: 0.7;
}
.sliderfull-items__button-img:active {
  opacity: 0.3;
}
.sliderfull-indicators {
  z-index: 1;
  max-width: 350px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}
.sliderfull-indicators__item {
  list-style: none;
}
.sliderfull-indicators__button {
  margin: 0 3px;
  width: 40px;
  height: 10px;
  border: none;
  border-radius: 8px;
  background-color: #fff;
  opacity: 0.5;
  transition: 0.4s ease;
  cursor: pointer;
}
.sliderfull-indicators__button--active {
  background-color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.sliderfull-indicators__button:hover,
.sliderfull-indicators__button:focus {
  opacity: 0.8;
}
.sliderfull-indicators__button:active {
  transition: 0s;
  opacity: 1;
}
.sliderfull-items {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
.sliderfull-items__inner {
  width: 100%;
  height: 100%;
  display: flex;
  transition: 1s all;
}
.sliderfull-items__item {
  text-align: center;
}
.sliderfull-items__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.projects {
  display: grid;
  list-style: none;
  grid-template-columns: repeat(3, 1fr);
  padding: 0 20px;
}
@media (max-width: 1100px) {
  .projects {
    grid-template-columns: repeat(2, 1fr);
    padding: 0;
  }
}
@media (max-width: 900px) {
  .projects {
    grid-template-columns: repeat(1, 1fr);
    padding: 0;
  }
}
.projects__card {
  width: 100%;
  height: 400px;
  position: relative;
}
.projects__card:hover .projects__image,
.projects__card:focus .projects__image,
.projects__card--scrolled .projects__image {
  -webkit-filter: grayscale(0);
  filter: grayscale(0);
}
.projects__card:hover .projects__subtitle,
.projects__card:focus .projects__subtitle,
.projects__card--scrolled .projects__subtitle,
.projects__card--scrolled .projects__subtitle {
  transition: 0.3s all ease-in;
  opacity: 1;
}
@media (max-width: 900px) {
  .projects__card {
    height: 70vh;
    min-height: 400px;
  }
}
.projects__link {
  text-decoration: none;
  color: #ffffff;
}
.projects__info {
  position: absolute;
  z-index: 1;
  top: 20px;
  left: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.projects__title {
  color: #ffffff;
  text-transform: uppercase;
  font-size: 20px;
}
.projects__subtitle {
  color: #ffffff;
  transition: 0.3s all ease-in;
  opacity: 0;
}
.projects__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: flex;
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
